"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Navbar } from "@/components/navbar";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { User, Clock, Award, FileText, CheckCircle2, XCircle, Clock as ClockIcon, PenLine } from "lucide-react";
import Link from "next/link";

/**
 * Attempts to fetch dashboard info and feature support.  
 * If the username API route doesn't exist, disables editing.
 */
async function fetchDashboardData() {
  try {
    const res = await fetch("/api/dashboard-info", { cache: "no-store" });
    if (!res.ok) throw new Error("Dashboard fetch failed");
    const data = await res.json();
    let canEditUsername = false;
    // Test if username edit API route exists and is functional
    try {
      const probe = await fetch("/api/change-username", { method: "OPTIONS" });
      canEditUsername = probe.ok;
    } catch {
      canEditUsername = false;
    }
    return {
      ...data,
      canEditUsername,
    };
  } catch {
    // When dashboard fetch fails, just return a fallback not to break UI
    return {
      applicationStatus: null,
      isWhitelisted: false,
      minecraftUsername: "Not set",
      canEditUsername: false,
      userId: null,
    };
  }
}

function UsernameEdit({
  initialValue,
  onChanged,
  canEdit
}: {
  initialValue: string;
  onChanged: (newName: string) => void;
  canEdit: boolean;
}) {
  const [editing, setEditing] = useState(false);
  const [value, setValue] = useState(initialValue);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    setValue(initialValue);
    setEditing(false);
    setError(null);
  }, [initialValue]);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!value.trim()) {
      setError("Username cannot be empty");
      return;
    }
    setLoading(true);
    setError(null);

    try {
      const res = await fetch("/api/change-username", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ minecraftUsername: value.trim() })
      });
      const data = await res.json();
      if (!res.ok) {
        setError(data?.error || "Failed to update username");
      } else {
        setEditing(false);
        onChanged(value.trim());
      }
    } catch {
      setError("Failed to update username");
    } finally {
      setLoading(false);
    }
  }

  if (!editing) {
    return (
      <div className="flex items-center gap-2">
        <p className="font-medium">{value}</p>
        {canEdit && (
          <Button
            size="icon"
            variant="ghost"
            aria-label="Edit Username"
            onClick={() => setEditing(true)}
            className="ml-2"
            type="button"
          >
            <PenLine className="h-4 w-4" />
          </Button>
        )}
      </div>
    );
  }

  return (
    <form onSubmit={handleSubmit} className="flex items-center gap-2 w-full">
      <input
        type="text"
        required
        minLength={3}
        maxLength={16}
        value={value}
        onChange={e => setValue(e.target.value)}
        className="border rounded px-2 py-1 text-sm focus:outline-none focus:ring focus:border-primary"
      />
      <Button type="submit" size="sm" disabled={loading}>
        Save
      </Button>
      <Button type="button" size="sm" variant="ghost" onClick={() => setEditing(false)}>
        Cancel
      </Button>
      {error && (
        <span className="text-red-500 text-xs ml-2">{error}</span>
      )}
    </form>
  );
}

export default function DashboardPage() {
  const [state, setState] = useState<{
    isLoading: boolean;
    data: any;
  }>({
    isLoading: true,
    data: null,
  });

  const router = useRouter();

  useEffect(() => {
    let done = false;
    fetchDashboardData().then((data) => {
      if (!data || !data.userId) {
        router.replace("/sign-in");
        return;
      }
      setState({ isLoading: false, data });
    });
    return () => { done = true; };
  }, [router]);

  if (state.isLoading) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <main className="flex-1 flex items-center justify-center">
          <p className="text-lg text-muted-foreground">Loading...</p>
        </main>
      </div>
    );
  }

  const {
    applicationStatus,
    isWhitelisted,
    minecraftUsername,
    canEditUsername
  } = state.data;

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />
      <main className="flex-1 flex items-center justify-center">
        <div className="w-full max-w-[980px] px-4 py-8 md:py-12 lg:py-24 flex flex-col items-center justify-center">
          <div className="flex flex-col items-center gap-4 text-center w-full">
            <h1 className="text-3xl font-bold leading-tight tracking-tighter md:text-5xl lg:leading-[1.1]">
              Dashboard
            </h1>
            <p className="max-w-[750px] text-lg text-muted-foreground">
              Manage your account and view your server status
            </p>
          </div>

          <div className="grid gap-6 mt-8 w-full md:grid-cols-2 lg:grid-cols-3">
            {/* Account Status Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <User className="h-5 w-5 text-primary" />
                  <CardTitle>Account Status</CardTitle>
                </div>
                <CardDescription>Your account information</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Minecraft Username</p>
                  <UsernameEdit
                    initialValue={minecraftUsername || "Not set"}
                    canEdit={!!canEditUsername}
                    onChanged={newName => {
                      setState((s) => ({
                        ...s,
                        data: { ...s.data, minecraftUsername: newName }
                      }));
                    }}
                  />
                  {canEditUsername && (
                    <p className="text-xs text-muted-foreground mt-1">
                      You can change your username.<br />After submitting, your username will update instantly.
                    </p>
                  )}
                  {!canEditUsername && (
                    <p className="text-xs text-muted-foreground mt-1">
                      Username changing is currently unavailable.
                    </p>
                  )}
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Whitelist Status</p>
                  {isWhitelisted ? (
                    <Badge className="bg-green-500">
                      <CheckCircle2 className="h-3 w-3 mr-1" />
                      Whitelisted
                    </Badge>
                  ) : (
                    <Badge variant="secondary">
                      <XCircle className="h-3 w-3 mr-1" />
                      Not Whitelisted
                    </Badge>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Application Status Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <FileText className="h-5 w-5 text-primary" />
                  <CardTitle>Application Status</CardTitle>
                </div>
                <CardDescription>Your server application</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-2">Status</p>
                  {applicationStatus === "pending" && (
                    <Badge variant="secondary">
                      <ClockIcon className="h-3 w-3 mr-1" />
                      Pending Review
                    </Badge>
                  )}
                  {applicationStatus === "approved" && (
                    <Badge className="bg-green-500">
                      <CheckCircle2 className="h-3 w-3 mr-1" />
                      Approved
                    </Badge>
                  )}
                  {applicationStatus === "rejected" && (
                    <Badge variant="destructive">
                      <XCircle className="h-3 w-3 mr-1" />
                      Rejected
                    </Badge>
                  )}
                  {!applicationStatus && (
                    <Badge variant="outline">No Application</Badge>
                  )}
                </div>
                {!applicationStatus && (
                  <Link href="/apply">
                    <Button className="w-full" size="sm">
                      Submit Application
                    </Button>
                  </Link>
                )}
              </CardContent>
            </Card>

            {/* Quick Stats Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <Award className="h-5 w-5 text-primary" />
                  <CardTitle>Quick Stats</CardTitle>
                </div>
                <CardDescription>Your server statistics</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Playtime</p>
                  <p className="font-medium">0 hours</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">PVP Kills</p>
                  <p className="font-medium">0</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Rank</p>
                  <Badge variant="outline">Member</Badge>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Recent Activity */}
          <Card className="w-full mt-8">
            <CardHeader>
              <CardTitle>Recent Activity</CardTitle>
              <CardDescription>Your recent server activity</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8 text-muted-foreground">
                <Clock className="h-12 w-12 mx-auto mb-4 opacity-50" />
                <p>No recent activity</p>
                <p className="text-sm mt-2">Start playing to see your activity here</p>
              </div>
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <div className="w-full mt-8">
            <h2 className="text-2xl font-bold mb-4 text-center">Quick Actions</h2>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              <Link href="/apply">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">Submit Application</CardTitle>
                    <CardDescription>Apply to join the server</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
              <Link href="/leaderboard">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">View Leaderboard</CardTitle>
                    <CardDescription>See top players</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
              <Link href="/events">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">Upcoming Events</CardTitle>
                    <CardDescription>Check server events</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}